'use client'

import Link from 'next/link'
import { Zap, Mail, MessageSquare, Building } from 'lucide-react'

export default function ContactPage() {
  return (
    <div className="min-h-screen bg-gray-950 text-white">
      {/* Navigation */}
      <nav className="sticky top-0 z-50 border-b border-gray-800 bg-gray-950/80 backdrop-blur-xl">
        <div className="max-w-6xl mx-auto px-4">
          <div className="flex items-center justify-between h-16">
            <Link href="/" className="flex items-center gap-2">
              <div className="w-8 h-8 rounded-lg bg-lime-400 flex items-center justify-center">
                <Zap className="w-5 h-5 text-gray-900" />
              </div>
              <span className="text-lg font-bold text-white">NexFlow</span>
            </Link>
            <Link href="/" className="text-gray-400 hover:text-white">
              ← Back to Home
            </Link>
          </div>
        </div>
      </nav>

      <main className="py-20">
        <div className="max-w-4xl mx-auto px-4">
          <div className="text-center mb-12">
            <h1 className="text-4xl font-bold mb-4">Contact Us</h1>
            <p className="text-xl text-gray-400 max-w-2xl mx-auto">
              Interested in building on NexFlow infrastructure? 
              Let's discuss how we can help.
            </p>
          </div>

          <div className="grid md:grid-cols-2 gap-8">
            {/* Contact Form */}
            <div className="bg-gray-900 p-8 rounded-2xl border border-gray-800">
              <h2 className="text-xl font-semibold mb-6">Send us a message</h2>
              <form className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-1">Name</label>
                  <input 
                    type="text" 
                    className="w-full px-4 py-3 bg-gray-800 border border-gray-700 rounded-lg focus:ring-2 focus:ring-lime-400 focus:border-lime-400 text-white"
                    placeholder="Your name"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-1">Email</label>
                  <input 
                    type="email" 
                    className="w-full px-4 py-3 bg-gray-800 border border-gray-700 rounded-lg focus:ring-2 focus:ring-lime-400 focus:border-lime-400 text-white"
                    placeholder="you@company.com"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-1">Subject</label>
                  <select className="w-full px-4 py-3 bg-gray-800 border border-gray-700 rounded-lg focus:ring-2 focus:ring-lime-400 focus:border-lime-400 text-white">
                    <option>Partnership Inquiry</option>
                    <option>Technical Integration</option>
                    <option>Enterprise Pricing</option>
                    <option>General Question</option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-1">Message</label>
                  <textarea 
                    rows={4}
                    className="w-full px-4 py-3 bg-gray-800 border border-gray-700 rounded-lg focus:ring-2 focus:ring-lime-400 focus:border-lime-400 text-white"
                    placeholder="Tell us about your project..."
                  />
                </div>

                <button 
                  type="submit"
                  className="w-full bg-lime-400 text-gray-900 py-3 rounded-lg font-semibold hover:bg-lime-300"
                >
                  Send Message
                </button>
              </form>
            </div>

            {/* Contact Info */}
            <div className="space-y-6">
              <div className="bg-gray-900 p-6 rounded-xl border border-gray-800">
                <div className="flex items-center gap-4">
                  <div className="w-12 h-12 bg-lime-400/10 rounded-lg flex items-center justify-center">
                    <Mail className="w-6 h-6 text-lime-400" />
                  </div>
                  <div>
                    <h3 className="font-semibold">Email</h3>
                    <a href="mailto:support@nexflowapp.app" className="text-lime-400 hover:underline">
                      support@nexflowapp.app
                    </a>
                  </div>
                </div>
              </div>

              <div className="bg-gray-900 p-6 rounded-xl border border-gray-800">
                <div className="flex items-center gap-4">
                  <div className="w-12 h-12 bg-purple-400/10 rounded-lg flex items-center justify-center">
                    <MessageSquare className="w-6 h-6 text-purple-400" />
                  </div>
                  <div>
                    <h3 className="font-semibold">Twitter / X</h3>
                    <a href="https://twitter.com/nexflow" className="text-lime-400 hover:underline">
                      @nexflow
                    </a>
                  </div>
                </div>
              </div>

              <div className="bg-gray-900 p-6 rounded-xl border border-gray-800">
                <div className="flex items-center gap-4">
                  <div className="w-12 h-12 bg-blue-400/10 rounded-lg flex items-center justify-center">
                    <Building className="w-6 h-6 text-blue-400" />
                  </div>
                  <div>
                    <h3 className="font-semibold">Enterprise</h3>
                    <p className="text-gray-400 text-sm">
                      For custom integrations and volume pricing
                    </p>
                    <a href="mailto:support@nexflowapp.app" className="text-lime-400 hover:underline text-sm">
                      support@nexflowapp.app
                    </a>
                  </div>
                </div>
              </div>

              <div className="bg-gradient-to-r from-lime-400/10 to-blue-400/10 p-6 rounded-xl border border-lime-400/20">
                <h3 className="font-semibold mb-2">Built on NexFlow</h3>
                <p className="text-gray-400 text-sm mb-3">
                  See NexFlow in action with EasePay, our merchant payment product.
                </p>
                <Link href="https://easepay.xyz" className="text-lime-400 hover:underline text-sm">
                  Visit EasePay →
                </Link>
              </div>
            </div>
          </div>
        </div>
      </main>

      {/* Footer */}
      <footer className="border-t border-gray-800 py-8">
        <div className="max-w-6xl mx-auto px-4 text-center">
          <p className="text-gray-600 text-sm">© 2025 NexFlow. Smart payment infrastructure.</p>
        </div>
      </footer>
    </div>
  )
}
